class MoreSkills extends Component{
	constructor(props) {
	    super(props);
	    this.showSkills = true;
	    this.isEnd = true;
	}
	toggleSkills = ()=> {
		let oSkills = this.skillCircle,
			aBox = oSkills.querySelectorAll('.sBox');

		if(!this.isEnd) return;
			this.isEnd = false;
		if(aBox.length === 0){
			//技能数组
	    	let arr=["ajax","html5、css3","canvas","es6","webpack","react native","git","sass","nodejs"];
			
			for(let i=0;i<arr.length;i++){
				let oBox = document.createElement('div');
				oBox.innerHTML = arr[i];
				oBox.className = 'sBox';
				oSkills.appendChild(oBox);
				oBox.radian = 0;
				this.move(oBox, i * (360 / arr.length),1000,()=>{
					this.isEnd = true;
				});
			}
		}else {
			if(this.showSkills){
				for(let i=0;i<aBox.length;i++){
					this.move(aBox[i],0,1000,()=>{
						aBox[i].style.display = 'none';
						this.isEnd = true;
					});
				}
			}else{
				for(let i=0;i<aBox.length;i++){
					aBox[i].style.display = 'block';
					this.move(aBox[i], i * (360 / aBox.length),1000,()=>{
						this.isEnd = true;
					});
				}
			}
			this.showSkills = !this.showSkills;
		}
	}
	move = (obj,target,time,fn)=> {
		let n = 0,
			start = obj.radian,
			count = Math.round(time/16.6),
			dis = target - start;
		obj.RAF && cancelAnimationFrame(obj.RAF);
		var loop = ()=>{
			n++;
			if(n === count){
				fn && fn();
				cancelAnimationFrame(obj.RAF);
				return;
			}
			let cur = start + dis*n/count;
			obj.radian = cur;
			this.setPos(obj,cur);
			obj.RAF = requestAnimationFrame(loop);
		}
		obj.RAF = requestAnimationFrame(loop);
	}
	setPos = (obj,ang)=> {
		let W = this.skillCircle.offsetWidth,
			H = this.skillCircle.offsetHeight;
		let x = Math.sin(a2r(ang)) * (W/2-obj.offsetWidth/2),
			y = H/2 - Math.cos(a2r(ang)) * (H/2-obj.offsetHeight/2);
		obj.style.transform = `translate(${x}px,${y}px)`;
	}
	render() {
		return (
			<div className="skill-more">
				<img className="skill-more-bg" src={require("../images/main/creative.jpg")} alt=""/>
				<div className="skill-circle-wp">
					<p className="skill-circle-tit">More skill/更多技能</p>
					<div className="skill-circle" id="skillCircle" ref = {(node)=>{this.skillCircle = node;}}>
						<div className="skill-circle-btn" onClick = {this.toggleSkills}></div>
					</div>
				</div>
			</div>
		)
	}
}

