class Works extends Component {
	tabWork(index) {
		store.dispatch({type:'tabWork',payload:index});
		this.timeStamp = Date.now();
		this.autoTab();
	}
	autoTab = () => {
		let interval = 5000;
		if(requestAnimationFrame){

			if(Date.now()- this.timeStamp >= interval){
				let index = store.getState().curTabIndex === 0 ? 1 : 0;
				store.dispatch({type:'tabWork',payload:index});
				this.timeStamp = Date.now();

			}
			this.timer && cancelAnimationFrame(this.timer);
			this.timer = requestAnimationFrame(this.autoTab);	
		}else{
			this.timer && clearInterval(this.timer);
			this.timer = setInterval(()=>{
				let index = store.getState().curTabIndex === 0 ? 1 : 0;
				store.dispatch({type:'tabWork',payload:index});
			},interval);
		}
		
	}
	componentWillMount() {
		this.unsubscribe = store.subscribe(()=>{
			this.forceUpdate();
		})
	}
	componentDidMount() {
		this.timeStamp = Date.now();
		this.autoTab();
	}
	render() {
		const curTabIndex = store.getState().curTabIndex;
		return(
			<section className="works-wp" id="myWorks">
				<div className="works-header">
					<p>My Works<em>我的作品</em></p>
					<a href="#portfolio" className="down-arrow-btn" onClick={(e)=>{scrollToAnchor.call(this,e,'workContWp')}}>
						<i className="iconfont icon-chevrondown"></i>
					</a>
				</div>
				<LazyLoad  height={788} offset={[-viewPortH/3, 0]}>
					<CSSTransition appear={true} in={true} timeout={1000} classNames="works-cont-wp">
						<div className="works-cont-wp" id="workContWp">
							<div className={'works-cont pos'+curTabIndex } 
								onMouseEnter = {()=>{cancelAnimationFrame(this.timer) || clearInterval(this.timer)}}
								onMouseLeave = {()=>{this.timeStamp = Date.now();this.autoTab()}}
							>
								<ul className="works-list">
									<WorksItem txt='webpack4.x配置指南'  bgUrl={require("../images/main/works7.jpg")} routeUrl="https://segmentfault.com/a/1190000015592264" />
									<WorksItem txt='小球碰撞' bgUrl={require("../images/main/works2.jpg")} routeUrl="ballCrash"/>
									<WorksItem txt='3D盒子'  bgUrl={require("../images/main/works1.jpg")} routeUrl="3dBox" />
									<WorksItem txt='捕鱼游戏' bgUrl={require("../images/main/works4.jpg")} routeUrl="fish" />
									<WorksItem txt='CSS3手工时钟' bgUrl={require("../images/main/works5.jpg")} routeUrl="css3Clock" />
									<WorksItem txt='谷歌商店拖拽图标' bgUrl={require("../images/main/works6.jpg")} routeUrl="chromeApp" />
								</ul>
								<ul className="works-list">
									<WorksItem txt='3D圆环' bgUrl={require("../images/main/works3.jpg")} routeUrl="3dCircle" />
									
									<WorksItem txt='canvas屏保' bgUrl={require("../images/main/works8.jpg")} routeUrl="screenPro"/>
									<WorksItem txt='未完待续...' bgUrl={require("../images/main/works9.jpg")} routeUrl=""/>
								</ul>
							</div>
							<div className="works-tab">
								{
									['tab-btn1','tab-btn2'].map((ele,index)=>{
										return (
											<span key={index} onClick={this.tabWork.bind(this,index)}
												className={index===curTabIndex?'works-tab-btn active':'works-tab-btn'}>
											</span>
										)
									})
								}
								
							</div>
						</div>
					</CSSTransition>
				</LazyLoad>
			</section>
		)
	}
	componentWillUnmount() {
		this.unsubscribe();
		this.timer && (cancelAnimationFrame(this.timer) || clearInterval(this.timer));
	}
}
