import React , { Component} from 'react';

import '../css/3dBox.scss';

class TDBox extends Component {
	constructor(props) {
	  super(props);
	
	  this.state = {
	  	disX: 0,
	  	disY: 0,
	  	boxStyle: {}
	  };
	}
	rotateBox = (e)=>{
		e = e || window.event;
		let lastX = e.clientX - this.state.disX,
		    lastY = e.clientY - this.state.disY;
		    
		document.onmousemove = (ev)=>{
			ev = ev || window.event;
			let disX = ev.clientX - lastX,
				disY = ev.clientY - lastY;
			this.setState({
			  	disX: disX,
			  	disY: disY,
			  	boxStyle:{
					transform: `perspective(800px) rotateX(${-disY/5}deg) rotateY(${disX/5}deg)`
			  	}
			})
		}
		document.onmouseup = ()=>{
			document.onmousemove = document.onmouseup = null;
		}
	}
	render() {
		return(
			<div className= 'cube' style={this.state.boxStyle}
				onMouseDown = {this.rotateBox}
			>
				<div className="plane left">Left</div>
				<div className="plane right">Right</div>
				<div className="plane top">Top</div>
				<div className="plane bottom">Bottom</div>
				<div className="plane front">Front</div>
				<div className="plane back">Back</div>
			</div>
		)
	}
}

export default TDBox;