import React, { Component } from 'react';
import cvBg from '../images/fish/sea.png';

import {a2r,r2a,rnd} from '../common/tool';
const cvStyle = {
	background: `url(${cvBg})`,
	backgroundSize: '100% 100%',
	position: 'absolute',
	left: 0, top: 0, right: 0, bottom: 0,
	margin: 'auto'
}
class FishShoot extends Component{
	//碰撞检测

    fishInit() {
		//超类型
		function Sprite(img) {
			this.img = img;
			this.w = 0;
			this.h = 0;
			
			//源坐标起点(图片本身从哪开始截取)
			this.sx = 0;
			this.sy = 0;

			//目标坐标起点(图片放画板哪个位置)
			this.x = 0;
			this.y = 0;
			
			//移动速度
			this.speed = 0;
			//画板旋转角度
			this.rotate = 0;
		}
		Sprite.prototype = {
			draw: function(context){
				context.save();
				context.translate(this.x,this.y);
				context.rotate(a2r(this.rotate));
				context.drawImage(this.img,
					this.sx,this.sy,this.w,this.h,
					-this.w/2,-this.h/2,this.w,this.h);
				context.restore();
			},
			move: function(){
				var speedX = this.speed * Math.sin(a2r(this.rotate));
				var speedY = this.speed * Math.cos(a2r(this.rotate));
				this.x += speedX;
				this.y -= speedY;
			},
			collTest: function(obj) {
				if(obj === null) return;
				var r1 = Math.min(this.w,this.h)/2,
					r2 = Math.min(obj.w,obj.h)/2;
				var w = this.x - obj.x,
					h = this.y - obj.y;
				var dis = Math.sqrt(w*w + h*h);
				if(dis > (r1+r2)){
					return false;
				}else{
					return true;
				}
			}
		}
		Sprite.prototype.constructor = Sprite;
		
		//子类型鱼
		function Fish(imgs,type) {
			Sprite.call(this,imgs["fish"+type]);
			var size=[
				null,
				{w:37,h:55},
				{w:62,h:78},
				{w:54,h:72},
				{w:58,h:77},
				{w:122,h:107}
			];
			this.x = 50;
			this.w = size[type].w;
			this.h = size[type].h;
			
			this.rotate = 90;
			this.speed = rnd(2,6);

			this.count = 0;
		}
		//子类型鱼继承
		Fish.prototype = Object.create(Sprite.prototype);
		Fish.prototype.constructor = Fish;

		//拓展子类型鱼move方法
		var oldMove=Sprite.prototype.move;
		Fish.prototype.move=function () {
			oldMove.call(this);
			
			this.count++;
			if(this.count%5===0){
				this.sx+=this.w;
				if(this.sx===this.w*4){
					this.sx=0;
				}
			}
		};

		//子类型 炮 继承
		function Cannon(imgs,type) {
			Sprite.call(this,imgs['cannon' + type]);
			var size = [
				null,
				{w:74,h:78},
				{w:74,h:78},
				{w:74,h:78},
				{w:74,h:82},
				{w:74,h:88},
				{w:74,h:92},
				{w:74,h:96},
			]
			this.w = size[type].w;
			this.h = size[type].h;
		}
		Cannon.prototype = Object.create(Sprite.prototype);
		Cannon.prototype.constructor = Cannon;

		//子类型 炮弹 继承
		function Bullet(imgs,type) {
			Sprite.call(this,imgs['bullet']);
			var size = [
				null,
				{w:25,h:26,sx:87,sy:0},
				{w:25,h:29,sx:61,sy:0},
				{w:27,h:31,sx:32,sy:35},
				{w:29,h:33,sx:31,sy:82},
				{w:30,h:34,sx:30,sy:0},
				{w:31,h:35,sx:0,sy:82},
				{w:31,h:38,sx:0,sy:44}
			];
			this.w = size[type].w;
			this.h = size[type].h;
			this.sx = size[type].sx;
			this.sy = size[type].sy;
			this.speed = 2;
		}
		Bullet.prototype = Object.create(Sprite.prototype);
		Bullet.prototype.constructor = Bullet;
		
		//图片加载
		function loadImages(imgsAddrObj,fn) {
			var count = 0;
			var len = 0;
			var imgs = {};

			for(var name in imgsAddrObj){
				len++;
				var imgObj = new Image();
				imgObj.src = imgsAddrObj[name];
				imgs[name] = imgObj;
				imgObj.onload = function() {
					count++;
					if(count === len){
						fn && fn(imgs);
					}
				}
			}
		}
		
		var oC = document.getElementById('c1');
		var timer;
		if(oC.getContext){
			var context = oC.getContext('2d');

			loadImages({
				fish1:require("../images/fish/fish1.png"),
				fish2:require("../images/fish/fish2.png"),
				fish3:require("../images/fish/fish3.png"),
				fish4:require("../images/fish/fish4.png"),
				fish5:require("../images/fish/fish5.png"),
				cannon1:require("../images/fish/cannon1.png"),
				cannon2:require("../images/fish/cannon2.png"),
				cannon3:require("../images/fish/cannon3.png"),
				cannon4:require("../images/fish/cannon4.png"),
				cannon5:require("../images/fish/cannon5.png"),
				cannon6:require("../images/fish/cannon6.png"),
				cannon7:require("../images/fish/cannon7.png"),
				bottom:require("../images/fish/bottom.png"),
				bullet:require("../images/fish/bullet.png")
			},function(imgs) {
				//创建炮台
				var battery = new Sprite(imgs['bottom']);
				battery.w = 765;
				battery.h = 70;
				battery.x = oC.width/2;
				battery.y = oC.height - battery.h/2;
				
				//创建炮
				var n = rnd(1,8);
				var cannon = new Cannon(imgs,n);
				cannon.x = 440;
				cannon.y = oC.height - battery.h/2;
				//炮旋转
				oC.addEventListener('mousemove',function(ev){
					ev = ev || window.event;
					var x = ev.pageX - (cannon.x + oC.offsetLeft);
					var y = (cannon.y + oC.offsetTop) - ev.pageY;
					cannon.rotate = 90 - r2a(Math.atan2(y,x));
				},false);
				
				//添加炮弹
				var bArr = [];
				oC.addEventListener('mousedown',function(ev){
					var bullet = new Bullet(imgs,n);
					bullet.x=cannon.x;
					bullet.y=cannon.y;
					
					bullet.speed=5;
					bullet.rotate=cannon.rotate;
					bullet.ev = ev;
					bArr.push(bullet);
				},false);

				//创建鱼
				var fArr = [];
				setInterval(function() {
					var f = new Fish(imgs,rnd(1,6));
					f.y = rnd(20,oC.height-battery.h);
					fArr.push(f);
				},1000);

				timer && clearInterval(timer);
				timer = setInterval(function() {
					context.clearRect(0,0,oC.width,oC.height);

					//画鱼
					for(var i=0;i<fArr.length;i++){
						fArr[i].draw(context);
						fArr[i].move();
					}
					//画炮弹
					for(var j=0;j<bArr.length;j++){
						bArr[j].draw(context);
						bArr[j].move();
					}
					
					//画炮台
					battery.draw(context);

					//画炮
					cannon.draw(context);
					
					//碰撞检测
					for(var m=0;m<fArr.length;m++){
						for(var n=0;n<bArr.length;n++){
							var isColl = fArr[m] ? fArr[m].collTest(bArr[n]) : false;
							if(isColl) {
								fArr.splice(m--,1);
								bArr.splice(n--,1);
							}
						}
					}
				},30);
				
			})
		}
	}
	render() {
		return(
			<canvas style={cvStyle} id = "c1" width="800" height="600"></canvas>
		)
	}
	componentDidMount() {
		this.fishInit.call(this);
	}
}

export default FishShoot;