import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';

import App from './pageList/App';
import BallCrash from './pageList/BallCrash';
import Fish from './pageList/Fish';
import TDBox from './pageList/3DBox';
import TDCircle from './pageList/3DCircle';
import Css3Clock from './pageList/Css3Clock';
import ChromeApp from './pageList/ChromeApp';
import ScreenPro from './pageList/ScreenPro';

const Page = () => (
  <Router>
  	<Switch>
      <Route exact path="/" component={App} />
      <Route path="/ballCrash" component={BallCrash} />
      <Route path="/fish" component={Fish} />
      <Route path="/3dBox" component={TDBox} />
      <Route path="/3dCircle" component={TDCircle} />
      <Route path="/css3Clock" component={Css3Clock} />
      <Route path="/chromeApp" component={ChromeApp} />
      <Route path="/screenPro" component={ScreenPro} />
    </Switch>
  </Router>
);

ReactDOM.render(
	<Page />,document.getElementById('page')
)