import React, {Component} from 'react';
import '../css/chromeApp.scss';
import {move} from '../common/tool';

//图片加载
function loadImages(imgsArr,fn) {
	var count = 0;
	var len = 0;
	var imgs = [];

	for(var name in imgsArr){
		len++;
		var imgObj = new Image();
		imgObj.src = imgsArr[name];
		imgObj.alt = '';
		imgs.push(imgObj);
		imgObj.onload = function() {
			count++;
			if(count === len){
				fn && fn(imgs);
			}
		}
	}
}
class ChromeApp extends Component{
	componentDidMount() {
		loadImages([
				require("../images/chromeApp/1.png"),
				require("../images/chromeApp/2.png"),
				require("../images/chromeApp/3.png"),
				require("../images/chromeApp/4.png"),
				require("../images/chromeApp/5.png"),
				require("../images/chromeApp/6.png"),
				require("../images/chromeApp/7.png"),
				require("../images/chromeApp/8.png"),
				require("../images/chromeApp/9.png")
			],(imgs)=> {
				this.init(imgs);
			}
		);
	}
	init(imgs) {
	//布局转换，添加拖拽，碰撞检测（collTest）
		var oUl=document.getElementById("ul1");
		var zIndex=2;
		//布局转换
		var aPos=[];
		for(var i=0;i<imgs.length;i++){
			var oLi = document.createElement('li');
			oLi.appendChild(imgs[i]);
			oUl.appendChild(oLi);

			aPos.push({left:oLi.offsetLeft,top:oLi.offsetTop});
			oLi.style.left=aPos[i].left+'px';
			oLi.style.top=aPos[i].top+'px';
		}
		var aLi=oUl.children;
		for(var j=0;j<aLi.length;j++){
			aLi[j].style.position='absolute';
			aLi[j].style.margin=0;
			aLi[j].style.float = 'none';
			aLi[j].index=j;
		}
		for(var n=0;n<aLi.length;n++){
			drag(aLi[n]);
		}
		//拖拽函数
		function drag (obj) {
			obj.onmousedown=function (ev) {
				obj.style.zIndex=zIndex++;
				var oEvt=ev||window.event;
				var disX=oEvt.clientX-obj.offsetLeft;
				var disY=oEvt.clientY-obj.offsetTop;
				document.onmousemove=function (ev) {
					var oEvt=ev||window.event;
					obj.style.left=oEvt.clientX-disX+'px';
					obj.style.top=oEvt.clientY-disY+'px';
					
					var nearObj=findNearest(obj);
					if(nearObj&&nearObj!==obj){
						/*//交换位置
						move(nearObj,aPos[obj.index]);	
						//交换下标		
						var tmp=obj.index;
						obj.index=nearObj.index;
						nearObj.index=tmp;*/
						var n=obj.index;//拖拽着的obj索引
						var m=nearObj.index;//被撞的最近obj的索引
						for(var i=0;i<aLi.length;i++){
							if(aLi[i].index>n&&aLi[i].index<=m){
								aLi[i].index--;
								move(aLi[i],aPos[aLi[i].index]);
							}else if(aLi[i].index<n&&aLi[i].index>=m){
								aLi[i].index++;
								move(aLi[i],aPos[aLi[i].index]);
							}
						}
						obj.index=m;
					}
				}
				document.onmouseup=function () {
					document.onmousemove=document.onmouseup=null;
					
					move(obj,aPos[obj.index]);//没撞到，回自己原来位置
					
					obj.releaseCapture&&obj.releaseCapture();
				}
				obj.setCapture&&obj.setCapture();
				return false;
			}
		}
		//碰撞检测函数
		function collTest (obj1,obj2) {
			var l1=obj1.offsetLeft;
			var t1=obj1.offsetTop;
			var r1=obj1.offsetLeft+obj1.offsetWidth;
			var b1=obj1.offsetTop+obj1.offsetHeight;
			var l2=aPos[obj2.index].left;
			var t2=aPos[obj2.index].top;
			var r2=aPos[obj2.index].left+obj2.offsetWidth;
			var b2=aPos[obj2.index].top+obj2.offsetHeight;
			if(r1<l2||t1>b2||l1>r2||b1<t2){
				return false;
			}else{
				return true;
			}
		}
		
		//找最近函数
		function findNearest (obj) {
			var minDis=999999999999;
			var minDisIndex=-1;
			for(var i=0;i<aLi.length;i++){
				if(collTest(obj,aLi[i])){//撞到了
					//console.log('peng')
					//找最近
					var dis=getDis(obj,aLi[i]);
					if(dis<minDis){
						minDis=dis;
						minDisIndex=i;
					}
				}
			}
			if(minDisIndex===-1){
				return null;
			}else{//console.log(minDis)
				return aLi[minDisIndex];
				
			}
		}
		//获取距离函数
		function getDis (obj1,obj2) {
			var a=aPos[obj2.index].top-obj1.offsetTop;
			var b=aPos[obj2.index].left-obj1.offsetLeft;
			return	Math.sqrt(a*a+b*b);
		}

	}
	render() {
		return(
			<ul  id="ul1"></ul>
		)
	}
}

export default ChromeApp;