function a2r(ang) {
	//弧长等于半径的圆弧，所对应的圆心角为1弧度
	//则弧长等于2πr时，对应的圆心角为 2π 个弧度，此时对应的角度为360度
	//2π弧度 = 360度    1弧度 = 180度/π   1度 = π弧度/180
	return Math.PI/180 * ang ;
}
function r2a(radian) {
	return 180/Math.PI * radian;
}
function iTop(obj){
	let iTop = obj.offsetTop;
    while(obj.offsetParent){
		obj = obj.offsetParent;
    	iTop += obj.offsetTop;
	}
	return iTop
}

//随机整数
function rnd(n,m) {
	return Math.floor(n + Math.random()*(m-n));
}

//锚点平滑定位
function scrollToAnchor(e,anchorId) {
	if(anchorId){
		var oEvent = e || window.event;
		let anchorEle = document.getElementById(anchorId);
		if(anchorEle && anchorEle.scrollIntoView){
			anchorEle.scrollIntoView({behavior:'smooth',block:'start'});
			oEvent.preventDefault();
		}
	}
}

function getStyle (obj,attr) {
	return obj.currentStyle?obj.currentStyle[attr]:getComputedStyle(obj,false)[attr];
}

function move (obj,json,options) {
	options=options||{};
	options.time=options.time||300;
	options.type=options.type||'ease-out';
	
	var start={};
	var dis={};
	for(var name in json){
		start[name]=parseFloat(getStyle(obj,name));
		dis[name]=json[name]-start[name];
	}
	var count=Math.round(options.time/30);
	var n=0;
	
	clearInterval(obj.timer);
	obj.timer=setInterval(function () {
		n++;
		var cur;
		for(var key in json){
			switch(options.type){
				case 'linear':
					var a=n/count;
					cur=start[key]+dis[key]*a;
					break;
				case 'ease-in':
					var b=n/count;
					cur=start[key]+dis[key]*b*b*b;
					break;
				case 'ease-out':
					var c=1-n/count;
					cur=start[key]+dis[key]*(1-c*c*c);
					break;
				default:
					break;
			}
			if(key==='opacity'){
				obj.style.opacity=cur;
				obj.style.filter='alpha(opacity='+cur*100+')';
			}else{
				obj.style[key]=cur+'px';
			}
		}
		if(n===count){
			clearInterval(obj.timer);
			options.fn&&options.fn();
		}
	},30);
}

export {a2r,r2a,iTop,rnd,scrollToAnchor,getStyle,move};

