import { createStore } from 'redux';
const Reducer = (state={isNavCur:false,curTabIndex:1,posStyle:{transform:'translate(0,0)'}},action)=> {
	switch(action.type) {
		case 'toggleNav':
			return {...state,isNavCur:(state.isNavCur?false:true)};
		case 'tabWork':
			return {...state,curTabIndex:action.payload};
		case 'setPos':
			return Object.assign({},state,{posStyle:action.payload});
		default:
			return state;
	}
}
const store = createStore(Reducer);
export default store;