import React, { Component } from 'react';

import '../css/3dCircle.scss';

class TDCircle extends Component {
	init = ()=> {
		setTimeout(() => {
			let oUl = this.tdCircle;
			let aLi = oUl.getElementsByTagName('li');
			let len = aLi.length;
			for(let i=0;i<len;i++) {
				//创建阴影
				let oSpan = document.createElement('span'),
					oImg = document.createElement('img');
				oImg.src = require('../images/3DCircle/'+ (i+1) +'.jpg');
				
				oSpan.appendChild(oImg);
				aLi[i].appendChild(oSpan);
				//转出来
				aLi[i].style.transition = `all 1s ease ${(len-1-i)*200}ms`;

				aLi[i].style.transform = `rotateY(${360/len*i}deg) translateZ(300px)`
			}
			//设置透明度 （1-0|0-1）
			aLi[0].addEventListener('transitionend',()=>{
				for(let i=0;i<len;i++) {
					let cur = 360/len*i;
					if(cur>180) cur = 360-cur;  //0-180|180-0
					cur = (180 - cur)/180;
					if(cur<0.5) cur = 0.5;
					aLi[i].style.opacity = cur;
				}
			},false);

			let disX = 0,
				disY = 0,
				speedX = 0,
				speedY = 0,
				lastDisX = 0,
				lastDisY = 0,
				timer = null;

			oUl.onmousedown = (e)=> {
				e = e || window.event;
				
				let lastClientX = e.clientX - disX,
					lastClientY = e.clientY - disY;

				document.onmousemove = (e)=> {
					e = e || window.event;					
					
					//移动的距离
					disX = e.clientX - lastClientX;
					disY = e.clientY - lastClientY;

					//设置最大移动距离(角度)
					if(disY>600) disY=600;
					if(disY<-600) disY=-600;

					oUl.style.transform = `perspective(800px) rotateX(${-(disY/10+13)}deg) rotateY(${disX/10}deg)`;

					//设置甩出去的速度
					speedX = disX - lastDisX;
					lastDisX = disX;
					speedY = disY - lastDisY;
					lastDisY = disY;

				}
				document.onmouseup = ()=> {
					timer && clearInterval(timer);
					
					timer = setInterval(()=>{
						disX += speedX;
						disY += speedY;
						//设置最大移动距离(角度)
						if(disY>600) disY=600;
						if(disY<-600) disY=-600;
			
						//添加摩擦力
						speedX*=0.97;
						speedY*=0.97;

						oUl.style.transform = `perspective(800px) rotateX(${-(disY/10+13)}deg) rotateY(${disX/10}deg)`;
						
						//速度太小时清除定时器
						if(Math.abs(speedX)<=1 && Math.abs(speedY)<=1 ){
							clearInterval(timer);
						}
					},16.6);
					
					document.onmousemove=document.onmouseup=null;
				}
				return false;
			}
		},0)
	}
	render() {
		return (
			<ul id="tdCircle"
				ref={(node)=>{this.tdCircle = node;}}
			>
				<strong></strong>
				<li><img alt="" src={require("../images/3DCircle/1.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/2.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/3.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/4.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/5.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/6.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/7.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/8.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/9.jpg")} /></li>
				<li><img alt="" src={require("../images/3DCircle/10.jpg")} /></li>
			</ul>
		)
	}
	componentDidMount() {
		this.init();
	}
}

export default TDCircle;
